*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 68.75em) {
  html {
    font-size: 50%;
  }
}

body {
  color: #1e272e;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(90deg, #5dade2, #3498db);
  font-family: Poppins, sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

img {
  width: 100%;
}

.icon {
  list-style: 1;
}

.footer {
