  font-size: 2rem;
  transition: all .2s;
}

.color__button:hover {
  transform: translateY(-.5rem);
}

.color__red:hover {
  box-shadow: 0 0 1.5rem #ff3f3480;
}

.color__voilet:hover {
  box-shadow: 0 0 1.5rem #3c40c680;
}

.color__green:hover {
