  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  color: #1e272e;
  align-items: center;
  gap: .5rem;
  font-size: 2.5rem;
  font-weight: 400;
  display: flex;
}

.logo span {
  -webkit-background-clip: text;
  filter: hue-rotate(45deg);
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 700;
}

.color-icon {
  width: 2.5rem;
  height: 2.5rem;
}

.payment {
  cursor: pointer;
  align-items: center;
  gap: 1rem;
  padding: .5rem 1rem;
