.game {
  max-width: 100rem;
  height: 65rem;
  background-color: #f2f4f6;
  border-radius: 10px;
  grid-template-rows: 1.2fr 2fr .8fr 4fr .5fr;
  grid-template-columns: repeat(10, 1fr);
  padding: 0 4rem;
  display: grid;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2rem 3rem #00000014;
}

@media only screen and (max-width: 56.25em) {
  .game {
    max-width: 100vw;
    height: 100vh;
