  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: #000c;
  transition: all .3s ease-in-out;
  position: fixed;
}

.popup-active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.bet__container {
