}

.bet__heading--green {
  background-color: #05c46b;
}

.bet__amount {
  grid-template-columns: repeat(auto-fit, minmax(4rem, 1fr));
  gap: 2rem;
  margin: 4rem 6rem;
  display: grid;
}

@media only screen and (max-width: 37.5em) {
  .bet__amount {
    grid-template-columns: repeat(auto-fit, minmax(8rem, 1fr));
  }
}

.bet__amount-button {
  color: #f2f4f6;
  text-align: center;
  width: 80%;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 4px;
  justify-self: center;
  padding: .5rem 0;
  font-family: inherit;
  font-size: 1.8rem;
  transition: all .2s;
}

.bet__amount-button:hover {
  transform: translateY(-.5rem);
}

.bet__controls {
  height: 4rem;
  background: #f2f4f6;
