  font-size: 2.2rem;
}

.bet__controls-input:focus {
  outline: none;
}

.bet-button {
  justify-content: space-evenly;
  align-items: center;
  margin-top: 8rem;
  display: flex;
}

.bet__confirm, .bet__cancel {
  width: 12rem;
  text-align: center;
  color: #1e272e;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: .8rem;
  font-family: inherit;
  font-size: 1.8rem;
  transition: all .2s;
  box-shadow: 0 .8rem 1rem #0000000d;
}

.bet__confirm {
  color: #f2f4f6;
  background-color: #3498db;
  transition: background-color .2s;
}

.bet__confirm:hover {
  background-color: #2a7aaf;
}

.icon-sub, .icon-add {
  font-size: 2.4rem;
  line-height: 0;
}

.not-enough-money {
  text-align: center;
  color: #ff6f6f;
  pointer-events: none;
  opacity: 0;
  margin-top: 3.5rem;
  font-size: 1.6rem;
  transition: all .3s;
}

@keyframes shake {
  0% {
    left: -.5rem;
  }

  30% {
    left: .5rem;
  }

  50% {
    left: -.5rem;
  }

  70% {
    left: .5rem;
  }

  100% {
    left: -.5rem;
  }
}

.animation-active {
  animation: .5s linear reverse shake;
