.result {
  border: 1px solid #d2dae2;
  border-radius: 5px;
  grid-area: 4 / 1 / span 1 / -1;
  grid-template-rows: 5rem 1fr;
  grid-template-columns: repeat(4, 1fr);
  place-items: center;
  display: grid;
  position: relative;
  overflow: hidden;
}

.result__heading {
  color: #262431;
  letter-spacing: 1px;
  font-size: 1.8rem;
  font-weight: 400;
}

.result__container {
  width: 100%;
  height: 100%;
  border-top: 1px solid #d2dae2;
  grid-area: 2 / 1 / -1 / -1;
  overflow-x: hidden;
  overflow-y: scroll;
}

.result__details {
  height: 4.5rem;
  border-bottom: 1px solid #d2dae2;
  grid-template-columns: repeat(4, 1fr);
  place-items: center;
  font-size: 1.8rem;
  transition: all .2s;
