.result__details-result--green, .result__details-result--voilet, .result__details-result--red {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}

.result__details-result--red {
  background-color: #ff3f34;
}

.result__details-result--voilet {
  background-color: #3c40c6;
}

.result__details-result--green {
  background-color: #05c46b;
}

.loader {
  width: 30px;
  height: 30px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  background-color: #0000;
  border: 2px solid #d2dae2;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: 1s linear infinite loader;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

@keyframes loader {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader-active {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  overflow: visible;
